.contacts {
    padding: 55px 0 140px;
    &__container {
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    &__title {
        &-wrap {
            margin-bottom: 40px;
        }
        &-second {
            max-width: 515px;
            width: 100%;
            margin-bottom: 40px;
        }
    }

    &__block {
        max-width: 822px;
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    &__form {
        width: 100%;
        row-gap: 20px;
    }

    &__input {
        border: 1px solid var(--black-opacity-60);
        height: 40px;
        width: 100%;
        font-size: 2rem;
        line-height: 120%;
        color: var(--black-opacity-70);
        padding: 8px 20px;
        letter-spacing: -2%;

        &::placeholder {
            color: var(--black-opacity-70);
        }
    }

    &__textarea {
        height: 131px;
        resize: none;
    }

    &__inputs {
        display: flex;
        column-gap: 20px;
    }

    &__select {
        border-radius: 0;
        border: 1px solid var(--black-opacity-60);
        font-size: 2rem;
        line-height: 120%;
        letter-spacing: -2%;
        height: 40px;
        padding: 8px 20px;
        color: var(--black-opacity-70);
    }

    &__btn {
        margin-top: 20px;
    }

    &__success {
        position: absolute;
        top: 0;
        left: calc(-1px);
        right: calc(-1px);
        bottom: calc(-1px);
        background-color: var(--white);
        display: flex;
        justify-content: center;
        opacity: 0;
        visibility: hidden;
        transition: opacity .3s ease-in-out, visibility .3s ease-in-out;

        &-text {
            padding-top: 70px;
            max-width: 822px;
            width: 100%;
        }
    }

    &__success.active {
        opacity: 1;
        visibility: visible;
    }

}

@media screen and (max-width: 991px) {
    .contacts {
        padding: 55px 0 100px;

        &__success-text {
            padding-top: 60px;
        }
    }
}

@media screen and (max-width: 639px) {
    .contacts {
        padding: 40px 0 80px;

        &__title {
            &-wrap {
                margin-bottom: 30px;
            }

            &-second {
                margin-bottom: 30px;
                max-width: none;
            }
        }

        &__form {
            row-gap: 10px;
        }

        &__inputs {
            flex-direction: column;
            column-gap: 0px;
            row-gap: 10px;
        }

        &__btn {
            margin-top: 15px;
        }

        &__input {
            font-size: 1.6rem;
            padding: 10px 10px 11px;
        }

        &__select {
            font-size: 1.6rem;
            padding: 10px 10px 11px;
        }

        &__textarea {
            height: 72px;
        }

        &__success-text {
            padding-top: 50px;
        }
    }
}