.link-text {
    font-size: 2rem;
    line-height: 100%;
    font-weight: 300;
}

.descr {
    font-size: 2.8rem;
    line-height: 100%;
    letter-spacing: -2%;  
    font-weight: 300;
}

.descr-title-bold {
    font-size: 3.6rem;
    line-height: 100%;
    font-weight: 700;
}

.descr-bold {
    font-size: 2.8rem;
    line-height: 100%;
    font-weight: 700;
    letter-spacing: -2%;
}

.promo {
    font-size: 2.8rem;
    line-height: 100%;
    letter-spacing: -2%;
    font-weight: 700;
    color: var(--secondory);
}

.text-110 {
    font-weight: 300;
    font-size: 2.8rem;
    line-height: 110%;
    letter-spacing: -2%;
}

.text-120 {
    font-weight: 300;
    font-size: 2.8rem;
    line-height: 120%;
    letter-spacing: -2%;
}

.descr-white {
    font-size: 1.2rem;
    line-height: 100%;
    letter-spacing: -2%;
    color: var(--white);
    font-weight: 300;
}

@media screen and (max-width: 991px) {
    .link-text {
        font-size: 6.5rem;
        font-weight: 200;
    }
}

@media screen and (max-width: 639px) {
    .link-text {
        font-size: 3.2rem;
    }
    
    .descr {
        font-size: 1.6rem;
    }

    .descr-title {
        font-size: 2.4rem;
    }

    .descr-title-bold {
        font-size: 2.8rem;
    }

    .descr-bold {
        font-size: 2.4rem;
    }
    
    .promo {
        font-size: 2.4rem;
    }

    .text-110 {
        font-size: 1.6rem;
    }

    .descr-bold-min {
        font-size: 1.6rem;
    }

    .text-120 {
        font-size: 1.6rem;
    }
}