html {
  box-sizing: border-box;
  font-size: 10px;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

*:focus-visible {
  outline: 0;
}

body {
  height: 100%;
  font-family: var(--font-family);
  font-size: 10px;
  -webkit-text-size-adjust: 100%;
  margin: 0;
  min-width: 320px;
  min-height: 100vh;
  font-weight: 300;
  background-color: var(--white);
  color: var(--black);
}

body.lock {
  overflow: hidden;
}

main {
  display: flex;
  flex-direction: column;
  flex: 1 0 auto;
}


img {
  height: 100%;
  max-width: 100%;
  width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
  color: var(--black);
  cursor: pointer;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

p {
  font-size: 1.6rem;
  line-height: 155%;
}

.site-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  outline: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.custom-container {
  max-width: var(--content-width);
  margin: 0 auto;
  padding: 0 var(--container-offset);
}

@media screen and (max-width: 991px) {
  .custom-container {
    padding: 0 var(--container-offset-tablet);
  }
}

@media screen and (max-width: 639px) {
  .custom-container {
    padding: 0 var(--container-offset-mobile);
  }
}
.centered {
  text-align: center;
}

.btn-custom {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 40px;
  font-size: 2rem;
  line-height: 120%;
  font-weight: 700;
  color: var(--white);
  background-color: var(--black);
  transition: background-color .3s ease-in-out, color .3s ease-in-out;
}

.title {
  font-size: 6.5rem;
  line-height: 100%;
  font-weight: 200;
  padding: 0 35px;
  background-color: var(--white);
  &-wrap {
    display: flex;
    justify-content: center;
    position: relative;
    &::before {
      content: "";
      z-index: -1;
      width: 100%;
      height: 1px;
      background-color: var(--black);
      position: absolute;
      top: calc(50% - 2px);
      left: 0;
    }
  }
}

@media screen and (max-width: 639px) {
  .title {
    font-size: 3.2rem;
    padding: 0 10px;
  }
  
}